.info {
    width: 200px;
    height: 200px;
    background-color: #c5c5c5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 10px;
    border-radius: 10px;
    box-shadow: 10px 10px 10px #333;
  }
  
  .btn {
    padding: 5px 10px;
    border: 1px solid #333;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    overflow: hidden;
    transition: all 0.2s ease;
    box-shadow: 2px 2px 2px #333333b4;
  
    &:hover {
      color: #faa0a0;
    }
  
    &:active {
      position: relative;
      top: 2px;
      left: 2px;
      box-shadow: 0px 0px 0px #333333b4;
    }
  }
  