.form {
  max-width: 1000px;
  width: 100%;
  padding: 10px;
  font-size: 20px;
}

.fieldset {
    padding: 10px;
    display: grid;
    grid-template: auto 1fr / auto 1fr;
    gap: 20px 10px;
    align-items: center;
}

.textarea {
  resize: none;
  height: 100px;
  font-family: inherit;
  font-size: 20px;
}

.btn{
  cursor: pointer;
  margin-top: 10px;
  padding: 10px;
  margin-left: 10px;
}

.input{
  font-size: 20px;
}